const DB_NAME = 'FOOT';
let DB_VERSION = 1;
const DB_STORE_NAME = 'Record';
var indexedDB =
  window.indexedDB ||
  window.mozIndexedDB ||
  window.webkitIndexedDB ||
  window.msIndexedDB;
const log = e => {
  console.log(e);
};
const dbOp = {
  openDB(version) {
    return new Promise((resolve, reject) => {
      let request = indexedDB.open(DB_NAME, version);
      request.onerror = e => {
        log(e);
        reject(e);
      };
      request.onupgradeneeded = e => {
        console.log('Upgrading../');
        const db = e.target.result;
        if (!db.objectStoreNames.contains(DB_STORE_NAME)) {
          const store = db.createObjectStore(DB_STORE_NAME, {
            autoIncrement: true
          });
          store.createIndex('name', 'name', { unique: false });
        }
      };
      request.onsuccess = e => {
        log('Success!');
        resolve(e.target.result);
      };
    });
  },
  getStoreByName(db, name, type = false) {
    let transaction = db.transaction(name, type ? 'readwrite' : 'readonly');
    return transaction.objectStore(name);
  },
  openStore: async function(type) {
    const db = await this.openDB(DB_VERSION);
    const store = await this.getStoreByName(db, DB_STORE_NAME, type);
    return store;
  },
  get(store, key) {
    return new Promise((resolve, reject) => {
      const op = store.get(key);
      op.onsuccess = e => {
        resolve(e.target.result);
      };
      op.onerror = e => {
        resolve(null);
      };
    });
  },
  add(store, obj) {
    return new Promise((resolve, reject) => {
      const op = store.add(obj);
      op.onsuccess = e => {
        resolve('success');
      };
      op.onerror = log;
    });
  },
  put(store, value, key) {
    return new Promise((resolve, reject) => {
      const op = store.put(value, key);
      op.onsuccess = e => {
        resolve(true);
      };
      op.onerror = log;
    });
  },
  openCursor(store, range) {
    return new Promise((resolve, reject) => {
      let result = [];
      let op;
      if (range) {
        op = store.openCursor(range);
      } else {
        op = store.openCursor();
      }
      op.onsuccess = e => {
        const res = e.target.result;
        if (res) {
          result.push({
            primaryKey: res.primaryKey,
            key: res.key,
            value: res.value
          });
          res.continue();
        } else {
          resolve(result);
        }
      };
      op.onerror = e => {
        reject(e);
      };
    });
  },
  indexGet(store, key, name) {
    return new Promise((resolve, reject) => {
      const indexData = store.index(key);
      const op = indexData.get(name);
      op.onsuccess = res => {
        resolve(res.target.result);
      };
      op.onerror = e => {
        reject(e);
      };
    });
  },
  /*
   ,IDBKeyRangeIDBKeyRangeRange
   lowerBound: 
   upperBound
   bound 
   only 
   
   */
  indexFilter(store, name, range) {
    const indexData = store.index(name);
    let filterRange;
    if (range.start === range.end) {
      filterRange = IDBKeyRange.only(range.start);
    } else {
      filterRange = IDBKeyRange.bound(range.start, range.end);
    }
    // openCursor
    return this.openCursor(indexData, filterRange);
  },
  delete(store, key) {
    return new Promise((resolve, reject) => {
      const op = store.delete(key);
      op.onsuccess = e => {
        resolve(true);
      };
      op.onerror = e => {
        reject(false);
      };
    });
  },
  clear(store) {
    return new Promise((resolve, reject) => {
      const op = store.clear();
      op.onsuccess = e => {
        resolve(true);
      };
      op.onerror = e => {
        reject(false);
      };
    });
  }
};

export default {
  // async AddDataBase() {
  //   const db = await openDB(1);
  //   db.close();
  // },
  async add(obj) {
    const store = await dbOp.openStore(true);
    return dbOp.add(store, obj);
  },
  async get(key) {
    const store = await dbOp.openStore(true);
    return dbOp.get(store, key);
  },
  async put(obj, key) {
    const store = await dbOp.openStore(true);
    return dbOp.put(store, obj, key);
  },
  async openCursor() {
    const store = await dbOp.openStore(true);
    return dbOp.openCursor(store);
  },
  async indexGet(name, key) {
    const store = await dbOp.openStore(true);
    return dbOp.indexGet(store, name, key);
  },
  async indexFilter(name, range) {
    const store = await dbOp.openStore();
    return dbOp.indexFilter(store, name, range);
  },
  async delete(key) {
    const store = await dbOp.openStore();
    return dbOp.delete(store, key);
  }
};



